import React from 'react';

import { CSSTransition } from 'react-transition-group';
import CountUp from 'react-countup';

class Donate extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      select_status: '',
      value_donate: '',
      prev_value_donate: 0,
      prev_total_price: 0,
    };
    this.nodeRef = React.createRef();
  }

  countTotalPrice() {
    return this.state.value_donate * this.props.data.exchange + (this.state.select_status !== '' ? this.props.data.vip_status[this.state.select_status].price : 0);
  }

  confirmDonate() {
    console.log('click');
    mp.trigger('client:menuplayer:confirmDonate', { value_donate: this.state.value_donate, vip_status: this.state.select_status }); // eslint-disable-line
  }

  render() {
    return (
      <React.Fragment>
        <div className='menup__donate_title'>На данной странице вы можете приобрести игровую валюту и VIP статусы</div>
        <div className='menup__donate_box'>
          <div className='menup__donate_box_left'>
            <div className='menup__donate_balance'>
              <span className='menup__donate_balance_note'>Ваш баланс:</span>
              <span className='menup__donate_balance_text'>{this.props.data.balance} LC</span>
            </div>
            <div className='menup__donate_title_input'>Покупка игровой валюты</div>
            <input
              type='text'
              className='menup__donate_box_input_style'
              placeholder='Введите сумму'
              value={this.state.value_donate}
              maxLength='7'
              onChange={(e) => this.setState({ value_donate: e.target.value.replace(/\D+/g, '') })}
            />
            <div className='menup__donate_note_input'>
              Вы получите:{' '}
              <span className='menup__color_blue'>
                {this.state.value_donate !== '' ? (
                  <CountUp
                    duration={0.75}
                    start={this.state.prev_value_donate * parseInt(this.props.data.exchange)}
                    end={parseInt(this.state.value_donate) * parseInt(this.props.data.exchange)}
                    onEnd={() => {
                      this.setState({ prev_value_donate: parseInt(this.state.value_donate) });
                    }}
                  />
                ) : (
                  '0'
                )}
                $
              </span>
            </div>
            <CSSTransition nodeRef={this.nodeRef} in={this.state.value_donate !== '' || this.state.select_status !== ''} timeout={900} classNames='fade_zone' unmountOnExit>
              <div className='menup__donate_buy_boxes' ref={this.nodeRef}>
                <div className='menup__donate_buy_tittle'>Вы покупаете:</div>
                <div className='menuup__donate_boxes'>
                  {this.state.value_donate !== '' ? (
                    <div className='menup__donate_buy_donate'>
                      <CountUp
                        duration={0.75}
                        start={this.state.prev_value_donate * parseInt(this.props.data.exchange)}
                        end={parseInt(this.state.value_donate) * parseInt(this.props.data.exchange)}
                        onEnd={() => {
                          this.setState({ prev_value_donate: parseInt(this.state.value_donate) });
                        }}
                      />
                      $ игровой валюты
                    </div>
                  ) : null}

                  {this.state.select_status !== '' ? <div className='menup__donate_buy_status'>{this.props.data.vip_status[this.state.select_status].name} статус</div> : null}
                </div>
                <div className='menup__donate_buy_info'>
                  <span className='menup__color_blue_buy'>К оплате: </span>
                  <CountUp
                    duration={0.75}
                    start={this.state.prev_total_price}
                    end={parseInt(this.countTotalPrice())}
                    onEnd={() => {
                      this.setState({ prev_total_price: this.countTotalPrice() });
                    }}
                  />{' '}
                  LC
                </div>

                <div className='menup__donte_buy_button' onClick={() => this.confirmDonate()}>
                  Подтвердить
                </div>
              </div>
            </CSSTransition>
          </div>
          <div className='menup__donate_box_right'>
            <div className='menup__donate_box_right_tittle'>VIP статусы</div>
            <div className='menup__donate_box_right_box_box'>
              <div className='menup__donate_box_right_box1'>
                {Object.values(this.props.data.vip_status).map((e, i) => {
                  if ((i + 1) % 2 === 0) return null;
                  let index = `vipstatusbox${i}`;
                  return (
                    <div className='menup__donate_box_right_box' key={index}>
                      <div className='menup__donate_status_button'>
                        <input
                          type='radio'
                          id={index}
                          name='status_vip'
                          checked={this.state.select_status === e.id}
                          onClick={() => {
                            if (e.id === this.state.select_status) this.setState({ select_status: '' });
                          }}
                          onChange={() => {
                            this.setState({ select_status: e.id });
                          }}
                        />
                        <label htmlFor={index} className='menup__donate_status_button_input'>
                          <span>VIP {e.name} </span>
                          <span className='menup__donate_input_text'>
                            {e.price} <span className='menup__color_blue_buy'>LC</span>
                          </span>
                        </label>
                      </div>
                      <div className='menup__donate_status_info'>
                        {e.info.map((e, i) => {
                          let index = `info_status${i}`;
                          return (
                            <div className='menup__donate_status_info_text' key={index}>
                              {e}
                            </div>
                          );
                        })}
                      </div>
                    </div>
                  );
                })}
              </div>
              <div className='menup__donate_box_right_box2'>
                {Object.values(this.props.data.vip_status).map((e, i) => {
                  if ((i + 1) % 2 !== 0) return null;
                  let index = `vipstatusbox${i}`;
                  return (
                    <div className='menup__donate_box_right_box' key={index}>
                      <div className='menup__donate_status_button'>
                        <input
                          type='radio'
                          id={index}
                          name='status_vip'
                          checked={this.state.select_status === e.id}
                          onClick={() => {
                            if (e.id === this.state.select_status) this.setState({ select_status: '' });
                          }}
                          onChange={() => {
                            this.setState({ select_status: e.id });
                          }}
                        />
                        <label htmlFor={index} className='menup__donate_status_button_input'>
                          <span>VIP {e.name} </span>
                          <span className='menup__donate_input_text'>
                            {e.price} <span className='menup__color_blue_buy'>LC</span>
                          </span>
                        </label>
                      </div>
                      <div className='menup__donate_status_info'>
                        {e.info.map((e, i) => {
                          let index = `info_status${i}`;
                          return (
                            <div className='menup__donate_status_info_text' key={index}>
                              {e}
                            </div>
                          );
                        })}
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Donate;
