import React from 'react';

import './css/npcdialogue.css';
import EventManager from '../../EventManager';

class NpcDialogue extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      dialogue: {
        name: '',
        text: '',
        answers: [],
      },
    };
  }

  componentDidMount() {
    EventManager.addHandler('InitNpcDialogue', (value) => {
      this.setState((prev) => ({
        show: true,
        dialogue: {
          ...prev.dialogue,
          name: value.name,
          text: value.text,
          answers: value.answers,
        },
      }));
    });

    EventManager.addHandler('SetNpcDialogue', (value) => {
      if (value.name) {
        this.setState((prev) => ({
          dialogue: {
            ...prev.dialogue,
            name: value.name,
            text: value.text,
            answers: value.answers,
          },
        }));
      } else {
        this.setState((prev) => ({
          dialogue: {
            ...prev.dialogue,
            text: value.text,
            answers: value.answers,
          },
        }));
      }
    });

    EventManager.addHandler('CloseNpcDialogue', () => {
      this.setState((prev) => ({
        show: false,
        dialogue: {
          ...prev.dialogue,
          name: '',
          text: '',
          answers: '',
        },
      }));
    });
  }
  componentWillUnmount() {
    EventManager.removeHandler('InitNpcDialogue');
    EventManager.removeHandler('SetNpcDialogue');
    EventManager.removeHandler('CloseNpcDialogue');
  }

  selectedAnswer(id) {
    mp.trigger('NpcActionSelected', id); // eslint-disable-line
  }

  render() {
    if (!this.state.show) {
      return null;
    }
    return (
      <React.Fragment>
        <div className='dialogue__background'>
          <div className='dialogue__main'>
            <div className='dialogue__name'>{this.state.dialogue.name}:</div>
            <div className='dialogue__text'> {this.state.dialogue.text} </div>
            <div className='dialogue__answer'>
              {this.state.dialogue.answers.map((e, i) => {
                let index = `npcdialogue${e.id}`;
                return (
                  <div className='dialogue__answer_box' key={index} onClick={() => this.selectedAnswer(e.id)}>
                    <div className='dialogue__answer_blur'></div>
                    <div className='dialogue__answer_text'>{e.text}</div>
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default NpcDialogue;
