import React from 'react';

import EventManager from '../../../EventManager';

import Countdown from 'react-countdown';

class Capt extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      showUntilStart: false,
      defender: null,
      date: null,
      capt: {
        ballas: 0,
        bloods: 0,
        marabunta: 0,
        families: 0,
        vagos: 0,
      },
    };
  }

  componentDidMount() {
    EventManager.addHandler('InitGangWar', (value) => {
      this.setState((prev) => ({
        show: false,
        showUntilStart: true,
        defender: null,
        date: new Date().getTime() + value,
        capt: {
          ...prev.capt,
          ballas: 0,
          bloods: 0,
          marabunta: 0,
          families: 0,
          vagos: 0,
        },
      }));
    });

    EventManager.addHandler('StartGangWar', (value) => {
      this.setState((prev) => ({
        show: true,
        showUntilStart: false,
        defender: value.targetFractionId,
        date: new Date().getTime() + value.remainingMs,
        capt: {
          ...prev.capt,
          ballas: 0,
          bloods: 0,
          marabunta: 0,
          families: 0,
          vagos: 0,
        },
      }));
    });

    EventManager.addHandler('UpdateStats', (value) => {
      this.setState((prev) => ({
        capt: {
          ...prev.capt,
          ballas: value.ballas,
          bloods: value.bloods,
          marabunta: value.marabunta,
          families: value.families,
          vagos: value.vagos,
        },
      }));
    });

    EventManager.addHandler('HideCapt', () => {
      this.setState((prev) => ({
        show: false,
        showUntilStart: false,
        defender: null,
        date: null,
        capt: {
          ...prev.capt,
          ballas: 0,
          bloods: 0,
          marabunta: 0,
          families: 0,
          vagos: 0,
        },
      }));
    });
  }

  componentWillUnmount() {
    EventManager.removeHandler('InitGangWar');
    EventManager.removeHandler('StartGangWar');
    EventManager.removeHandler('UpdateStats');
    EventManager.removeHandler('HideCapt');
  }

  render() {
    if (this.state.showUntilStart) {
      return (
        <React.Fragment>
          <div className='hud_capt_positon'>
            <div className='hud__capt_timer_box'>
              До начала капта:&nbsp;
              <Countdown
                date={this.state.date}
                renderer={(props) => (
                  <div>
                    <div>
                      {('0' + props.minutes).slice(-2)}:{('0' + props.seconds).slice(-2)}
                    </div>
                  </div>
                )}
              />
            </div>
          </div>
        </React.Fragment>
      );
    } else if (this.state.show) {
      return (
        <React.Fragment>
          <div className='hud_capt_positon'>
            <div className='hud_capt_box'>
              <div className='hud_capt_box_li'>
                <div className={`hud_capt_box_li_count ${this.state.defender === 2 ? 'hud_capt_attaced' : ''}`} style={{ backgroundColor: `#923FA7` }}>
                  <span>{this.state.capt.ballas}</span>
                </div>
                <div className='hud_capt_box_li_text'>Ballas</div>
              </div>
              <div className='hud_capt_box_li'>
                <div className={`hud_capt_box_li_count ${this.state.defender === 1 ? 'hud_capt_attaced' : ''}`} style={{ backgroundColor: `#E33A41` }}>
                  <span>{this.state.capt.bloods}</span>
                </div>
                <div className='hud_capt_box_li_text'>Bloods</div>
              </div>
              <div className='hud_capt_box_li'>
                <div className={`hud_capt_box_li_count ${this.state.defender === 5 ? 'hud_capt_attaced' : ''}`} style={{ backgroundColor: `#1D9AE3` }}>
                  <span>{this.state.capt.marabunta}</span>
                </div>
                <div className='hud_capt_box_li_text'>Marabunta</div>
              </div>

              <div className='hud_capt_box_li'>
                <div className={`hud_capt_box_li_count ${this.state.defender === 3 ? 'hud_capt_attaced' : ''}`} style={{ backgroundColor: `#2C8F4E` }}>
                  <span>{this.state.capt.families}</span>
                </div>
                <div className='hud_capt_box_li_text'>The Families</div>
              </div>
              <div className='hud_capt_box_li'>
                <div className={`hud_capt_box_li_count ${this.state.defender === 4 ? 'hud_capt_attaced' : ''}`} style={{ backgroundColor: `#D7AE16` }}>
                  <span>{this.state.capt.vagos}</span>
                </div>
                <div className='hud_capt_box_li_text'>Vagos</div>
              </div>
            </div>
            <div className='hud__capt_timer_box'>
              <div className='hud__capt_timer_box_img'></div>
              <Countdown
                date={this.state.date}
                renderer={(props) => (
                  <div>
                    <div>
                      {('0' + props.minutes).slice(-2)}:{('0' + props.seconds).slice(-2)}
                    </div>
                  </div>
                )}
              />
            </div>
          </div>
        </React.Fragment>
      );
    }

    return null;
  }
}

export default Capt;
