import React from 'react';
import EventManager from '../../../EventManager';

class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      input_email: '',
      input_password: '',

      password_visibility: false,

      errors: {},
    };

    this.errors_mapping = new Map();
    this.errors_mapping.set('email', ['email', 'Неверный email']);
    this.errors_mapping.set('password', ['password', 'Неверный пароль']);
    this.errors_mapping.set('banned', ['email', 'Аккаунт заблокирован администратором']);
    this.errors_mapping.set('already_online', ['email', 'Данный аккаунт уже авторизован']);
    this.errors_mapping.set('wait', ['email', 'Подожди перед повторной попыткой']);
    this.errors_mapping.set('internal_server_error', ['email', 'Что-то пошло не так, попробуй еще раз']);
  }

  deleteError(field) {
    let errs = this.state.errors;
    delete errs[field];
    this.setState({ errors: errs });
  }

  componentDidUpdate(prevProp, prevState) {
    if (this.state.input_email !== prevState.input_email) {
      this.deleteError('email');
    }

    if (this.state.input_password !== prevState.input_password) {
      this.deleteError('password');
    }
  }

  componentDidMount() {
    EventManager.addHandler('LoginFailed', (value) => {
      let errs = {};

      value.forEach((item, _) => {
        let err = this.errors_mapping.get(item);
        errs[err[0]] = err[1];

        this.setState({ errors: errs });
      });
    });

    document.addEventListener('keydown', this.handleKeyPress);
  }

  componentWillUnmount() {
    EventManager.removeHandler('LoginFailed');

    document.removeEventListener('keydown', this.handleKeyPress);
  }

  handleKeyPress = (event) => {
    if (event.key === 'Enter') {
      this.login();
    }
  };

  login() {
    let request = {
      email: this.state.input_email,
      password: this.state.input_password,
    };

    mp.trigger('LoginSubmitted', JSON.stringify(request)); // eslint-disable-line
  }

  render() {
    return (
      <React.Fragment>
        <div className='signin__main'>
          <div className='signin__title'>Авторизация</div>
          <div className='signin__title_text'>Для игры на сервере введи данные ниже</div>
          <div className='signin__grid_box'>
            <div className={`signin__input_box ${this.state.errors.email ? 'signin__input_failed' : null}`}>
              <input type='text' className='signin__input' placeholder='Эл.почта' spellCheck='false' value={this.state.input_email} onChange={(e) => this.setState({ input_email: e.target.value })} />
              {this.state.errors.email ? (
                <div className='sigin__main_warning_box'>
                  <div className='sigin__warning_box'>
                    <div className='signin__img_failed'></div>
                  </div>
                  <div className='sigin_box_popup_text'>
                    <div className='sigin_popup_text'>{this.state.errors.email}</div>
                  </div>
                </div>
              ) : null}
            </div>
            <div className={`signin__input_box ${this.state.errors.password ? 'signin__input_failed' : null}`}>
              <input
                type={this.state.password_visibility ? 'text' : 'password'}
                className='signin__input'
                placeholder='Пароль'
                spellCheck='false'
                value={this.state.input_password}
                onChange={(e) => this.setState({ input_password: e.target.value })}
              />
              {this.state.errors.password ? (
                <div className='sigin__main_warning_box'>
                  <div className='sigin__warning_box'>
                    <div className='signin__img_failed'></div>
                  </div>
                  <div className='sigin_box_popup_text'>
                    <div className='sigin_popup_text'>{this.state.errors.password}</div>
                  </div>
                </div>
              ) : (
                <div
                  className='signin__input_visibility'
                  style={{ backgroundImage: `url(${require(`../img/icons/${this.state.password_visibility ? 'visibility_on' : 'visibility_off'}.svg`).default})` }}
                  onClick={() => this.setState({ password_visibility: !this.state.password_visibility })}
                ></div>
              )}
            </div>
          </div>
          <div className='signin__text_right' onClick={() => this.props.historyPush('/auth/reset-password')}>
            Забыл пароль?
          </div>
          <div className='signin__button' onClick={() => this.login()}>
            <div className='signin__button_blur'></div>
            <span className='signin__button_text'>Авторизоваться</span>
          </div>
          <div className='signin__text_signup' onClick={() => this.props.historyPush('/auth/register')}>
            Нет аккаунта? Зарегистрируйся!
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Login;
