import React from 'react';

import CountUp from 'react-countup';

import EventManager from '../../../EventManager';

class LineExperience extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      curent: 150,
      total: 500,
      prev_curent: 150,
    };
  }

  componentDidMount() {
    EventManager.addHandler('LineExperience', (value) => {
      if (value.type === 'show') {
        this.setState({ show: true });
      }
      if (value.type === 'hide') {
        this.setState({ show: false });
      } else return;
    });
  }

  componentWillUnmount() {
    EventManager.removeHandler('LineExperience');
  }

  render() {
    if (!this.state.show) {
      return null;
    }
    let percent = 100 / (this.state.total / this.state.curent);
    return (
      <React.Fragment>
        <div className='hud__lineexpr_position' style={{ bottom: `${197 / 10.8}vh`, left: `${4 / 10.8}vh` }}>
          <div className='hud__lineexpr_box'>
            <div className='hud__lineexpr_box_info'>
              <div className='hud__lineexpr_box_img'></div>
              <span>
                <CountUp
                  duration={0.75}
                  start={this.state.prev_curent}
                  end={this.state.curent}
                  onEnd={() => {
                    this.setState({ prev_curent: this.state.curent });
                  }}
                />
                /{this.state.total}
              </span>
            </div>

            <div className='hud__lineexpr_box_line' style={{ width: `${percent}%` }}></div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default LineExperience;
