import React from 'react';

class Body extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <React.Fragment>
        {this.props.data.type === 1 ? (
          <div className='notifi__main'>
            <div className='notifi__left_box'>
              <div className='notifi__left_box_img' style={{ backgroundImage: `url(${require(`../img/${this.props.data.notifi}.svg`).default})` }}></div>
            </div>
            <div className='notifi__text_box'>{this.props.data.text}</div>
            <div className='notifi__timer_box'>
              <div className='notifi__timer_line' style={{ '--duration': `${this.props.toastProps.autoClose}ms` }}></div>
            </div>
          </div>
        ) : null}
      </React.Fragment>
    );
  }
}

export default Body;
