import React from 'react';

import EventManager from '../../EventManager';

import Box from './components/Box';

import './css/weaponshop.css';

class WeaponShop extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      name_shop: 'Оружейный магазин',
      balance: '12500000',
      select_category: 0,
      category: {
        0: { id: 0, name: 'Все категории' },
        1: { id: 1, name: 'Ближний бой' },
        2: { id: 2, name: 'Пистолеты' },
        3: { id: 3, name: 'Винтовки' },
        4: { id: 4, name: 'Специальное' },
        5: { id: 5, name: 'Метательное' },
      },
      items: {
        // все предметы по категориям
        1: {
          knife: {
            id: 'knife',
            name: 'Нож',
            damage: '40',
          },
        },
        2: {
          deagle: {
            id: 'deagle',
            name: 'Deagle',
            damage: '65',
          },
          p45: {
            id: 'p45',
            name: 'P45',
            damage: '35',
          },
        },
        3: {
          ak47: {
            id: 'ak47',
            name: 'AK47',
            damage: '55',
          },
          m4a1: {
            id: 'm4a1',
            name: 'M4A1',
            damage: '50',
          },
        },
        4: {
          somethingSpecial: {
            id: 'somethingSpecial',
            name: 'Специальное',
            damage: '99',
          },
        },
        5: {
          grenade: {
            id: 'grenade',
            name: 'Граната',
            damage: '49',
          },
        },
      },
      shop_selling_items: {
        // ассортимент магазина (что в продаже)
        1: [
          {
            id: 'knife',
            price: 35,
            category: '1',
          },
        ],
        2: [
          {
            id: 'deagle',
            price: 150,
            category: '2',
          },
          {
            id: 'deagle',
            price: 150,
            category: '2',
          },
          {
            id: 'deagle',
            price: 150,
            category: '2',
          },
          {
            id: 'p45',
            price: 100,
            category: '2',
          },
        ],
        3: [
          { id: 'ak47', price: 250, category: '3' },
          { id: 'm4a1', price: 300, category: '3' },
        ],
        4: [
          {
            id: 'somethingSpecial',
            price: 999,
            category: '4',
          },
        ],
        5: [
          {
            id: 'grenade',
            price: 50,
            category: '5',
          },
        ],
      },
    };
  }

  componentDidMount() {
    EventManager.addHandler('WeaponShop', (value) => {
      if (value.type === 'show') {
        this.setState({ show: true });
      }
      if (value.type === 'hide') {
        this.setState({ show: false });
      }
      if (value.type === 'updateSellingItems') {
        this.setState({ shop_selling_items: value.items });
      } else return;
    });
  }
  componentWillUnmount() {
    EventManager.removeHandler('WeaponShop');
  }

  closeMenu() {
    mp.trigger('client:weaponshop:closeMenu'); // eslint-disable-line
  }

  buyItem(item) {
    console.log('clic');
    mp.trigger('client:weaponshop:buyItem', item); // eslint-disable-line
  }
  render() {
    if (!this.state.show) {
      return null;
    }
    return (
      <React.Fragment>
        <div className='wshop__position'>
          <div className='wshop__main'>
            <div className='wshop_blur'></div>
            <div className='wshop__box_title'>
              <div className='wshop__box_title_text'>{this.state.name_shop}</div>
              <div className='wshop__box_balance'>
                <div className='wshop__box_balance_text_grey'>Ваш текущий баланс:</div>
                <div className='wshop__box_balance_text_white'>{this.state.balance.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}$</div>
              </div>
              <div className='wshop__box_title_close' onClick={() => this.closeMenu()}></div>
            </div>
            <div className='wshop__category'>
              {Object.values(this.state.category).map((e, i) => {
                let index = `shop_category${i}`;
                return (
                  <div className='wshop__category_button' key={index}>
                    <input type='radio' id={index} name='shop_category' checked={e.id === this.state.select_category} onChange={() => this.setState({ select_category: e.id })} />
                    <label htmlFor={index} className='wshop__category_button_text'>
                      {e.name}
                    </label>
                  </div>
                );
              })}
            </div>
            <div className='wshop__content'>
              <div className='wshop__content_title'>{this.state.category[this.state.select_category].name}</div>
              <div className='wshop__grid'>
                {this.state.select_category === 0
                  ? Object.values(this.state.shop_selling_items).map((e, i) => {
                      return e.map((e, i) => {
                        let index = `shop__content${i}`;
                        return <Box items={this.state.items} category={e.category} item={e} key={index} buyItem={this.buyItem.bind(this)} />;
                      });
                    })
                  : this.state.shop_selling_items[this.state.select_category].map((e, i) => {
                      let index = `shop__content${i}`;
                      return <Box items={this.state.items} category={e.category} item={e} key={index} buyItem={this.buyItem.bind(this)} />;
                    })}
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default WeaponShop;
