import React from 'react';

import { CSSTransition } from 'react-transition-group';
import EventManager from '../../../EventManager';

class ResetPassword extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      input_email: '',
      show_popup: false,
      errors: {},
    };
    this.nodeRef = React.createRef();

    this.errors_mapping = new Map();
    this.errors_mapping.set('email', ['email', 'Введи существующий email']);
    this.errors_mapping.set('user_not_found', ['email', 'Пользователь с данным email не найден']);
    this.errors_mapping.set('wait', ['email', 'Подожди перед повторной попыткой']);
    this.errors_mapping.set('internal_server_error', ['email', 'Что-то пошло не так, попробуй еще раз']);
  }

  deleteError(field) {
    let errs = this.state.errors;
    delete errs[field];
    this.setState({ errors: errs });
  }

  componentDidUpdate(prevProp, prevState) {
    if (this.state.input_email !== prevState.input_email) {
      this.deleteError('email');
    }
  }

  componentDidMount() {
    EventManager.addHandler('ResetPasswordFailed', (value) => {
      let errs = {};

      value.forEach((item, _) => {
        if (item.includes('wait_')) {
          errs['email'] = 'Подожди ' + item.substring(item.indexOf('_') + 1) + ' секунд!';
        } else {
          let err = this.errors_mapping.get(item);
          errs[err[0]] = err[1];
        }

        this.setState({ errors: errs });
      });
    });

    EventManager.addHandler('ResetPasswordSucceed', () => {
      this.setState({ show_popup: true });
    });

    document.addEventListener('keydown', this.handleKeyPress);
  }

  componentWillUnmount() {
    EventManager.removeHandler('ResetPasswordFailed');
    EventManager.removeHandler('ResetPasswordSucceed');

    document.removeEventListener('keydown', this.handleKeyPress);
  }

  handleKeyPress = (event) => {
    if (event.key === 'Enter') {
      this.resetPassword();
    }
  };

  resetPassword() {
    let request = {
      email: this.state.input_email,
    };

    mp.trigger('ResetPasswordSubmitted', JSON.stringify(request)); // eslint-disable-line
  }

  render() {
    return (
      <React.Fragment>
        <div className='signup__main'>
          <div className='reset__title'>Восстановление доступа</div>
          <div className='signup__title_text'>Для восстановления доступа введи почту ниже</div>
          <CSSTransition in={this.state.show_popup} nodeRef={this.nodeRef} timeout={1000} classNames='alert' unmountOnExit>
            <div className='reset_popup' ref={this.nodeRef}>
              На указанную почту была выслана <br /> инструкция с дальнейшими действиями
            </div>
          </CSSTransition>

          {!this.state.show_popup && (
            <div className='signup__grid_box'>
              <div className={`signup__input_box ${this.state.errors.email ? 'signup__input_failed' : null}`}>
                <input
                  type='email'
                  className='signup__input'
                  placeholder='Эл.почта'
                  spellCheck='false'
                  value={this.state.input_email}
                  onChange={(e) => this.setState({ input_email: e.target.value })}
                />
                {this.state.errors.email ? (
                  <div className='sigin__main_warning_box'>
                    <div className='sigin__warning_box'>
                      <div className='signin__img_failed'></div>
                    </div>
                    <div className='sigin_box_popup_text'>
                      <div className='sigin_popup_text'>{this.state.errors.email}</div>
                    </div>
                  </div>
                ) : null}
              </div>
            </div>
          )}

          {!this.state.show_popup && (
            <div className='signup__button' onClick={() => this.resetPassword()}>
              <div className='signup__button_blur'></div>
              <span className='signup__button_text'>Восстановить</span>
            </div>
          )}

          <div className='signup__text_signin' onClick={() => this.props.historyPush('/auth/login')}>
            Авторизация
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default ResetPassword;
