import React from 'react';
import EventManager from '../../../EventManager';

class Logo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,
      online: '0',
    };
  }

  componentDidMount() {
    EventManager.addHandler('UpdateOnline', (value) => {
      this.setState({ online: value });
    });
  }

  componentWillUnmount() {
    EventManager.removeHandler('UpdateOnline');
  }

  render() {
    if (!this.state.show) {
      return null;
    }
    return (
      <React.Fragment>
        <div className='hud__logo_main'>
          <div className='hud__logo_img'></div>
          <div className='hud__logo_info'>Онлайн: {this.state.online}</div>
        </div>
      </React.Fragment>
    );
  }
}

export default Logo;
