import React from 'react';
import { Route, HashRouter as Router, Redirect } from 'react-router-dom';

import ResetPassword from './components/ResetPassword';
import Login from './components/Login';
import Register from './components/Register';
import EventManager from '../../EventManager';

import './css/auth.css';

class Auth extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      path: '/auth/login',
    };
  }

  componentDidMount() {
    EventManager.addHandler('Auth', (value) => {
      if (value.type === 'show') {
        this.setState({ show: true });
      }
      if (value.type === 'hide') {
        this.setState({ show: false });
      } else return;
    });
  }

  componentWillUnmount() {
    EventManager.removeHandler('Auth');
  }

  historyPush(customlink) {
    if (customlink === this.state.path) return;
    if (customlink !== undefined && customlink !== null && customlink !== '') {
      this.setState({ path: customlink });
      return;
    }
  }

  render() {
    if (!this.state.show) {
      return null;
    }
    return (
      <React.Fragment>
        <div className='auth__background' style={{ backgroundImage: `url(${require(`./img/background/1.png`).default})` }}>
          <div className='auth__help_text'>
            По нажатию на кнопку
            <div className='auth_help_box'>~</div>
            включается курсор
          </div>
          <div className='auth__main'>
            <div className='auth__main_logo'></div>
            <Router>
              <Route exact path='/auth/login'>
                <Login historyPush={this.historyPush.bind(this)} />
              </Route>
              <Route exact path='/auth/register'>
                <Register historyPush={this.historyPush.bind(this)} />
              </Route>
              <Route exact path='/auth/reset-password'>
                <ResetPassword historyPush={this.historyPush.bind(this)} />
              </Route>
              <Redirect to={this.state.path} push />
            </Router>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Auth;
