import React from 'react';
import { CSSTransition } from 'react-transition-group';
import EventManager from '../../../EventManager';

class PlayerHud extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,
      speedometer_visible: false,
      key: false,
      speed: 0,
      voice: false,
      money: 0,
      time: '00:00',
      date: '00.00',
      zoneShow: false,
      zoneColor: 'green', // green, red
    };
    this.nodeRef = React.createRef();
    this.nodeRef2 = React.createRef();
  }

  componentDidMount() {
    EventManager.addHandler('UpdateTime', (value) => {
      let time = value.hours + ':' + value.minutes;
      let date = value.day + '.' + value.month;

      this.setState({ time: time, date: date });
    });

    EventManager.addHandler('UpdateMoney', (value) => {
      this.setState({ money: value });
    });

    EventManager.addHandler('Voice', (value) => {
      if (value.type === 'show') {
        this.setState({ voice: true });
      }
      if (value.type === 'hide') {
        this.setState({ voice: false });
      } else return;
    });

    EventManager.addHandler('Speedometer', (value) => {
      if (value.type === 'show') {
        this.setState({ speedometer_visible: true });
      }
      if (value.type === 'hide') {
        this.setState({ speedometer_visible: false });
      } else return;
    });

    EventManager.addHandler('UpdateSpeedometer', (value) => {
      this.setState({ speed: value });
    });

    EventManager.addHandler('SetZoneState', (value) => {
      this.setState({ zoneShow: value.enabled, zoneColor: value.color });
    });
  }

  componentWillUnmount() {
    EventManager.removeHandler('UpdateTime');
    EventManager.removeHandler('UpdateMoney');
    EventManager.removeHandler('Voice');
    EventManager.removeHandler('Speedometer');
    EventManager.removeHandler('UpdateSpeedometer');
    EventManager.removeHandler('SetZoneState');
  }

  render() {
    if (!this.state.show) {
      return null;
    }
    return (
      <React.Fragment>
        <div className='hud__player_main'>
          <CSSTransition in={this.state.zoneShow} timeout={800} nodeRef={this.nodeRef} classNames='fade_zone' unmountOnExit>
            <div className='hud__player_zone' ref={this.nodeRef}>
              <div className={`hud__player_zone_${this.state.zoneColor}`}></div>
              <div className={`hud__player_zone_text hud__zone_color_${this.state.zoneColor}`}>
                {this.state.zoneColor === 'green' ? 'Мирная зона' : null}
                {this.state.zoneColor === 'red' ? 'Зона захвата' : null}
              </div>
            </div>
          </CSSTransition>
          <div className={`hud_player_box ${this.state.speedometer_visible ? 'hud_big' : null}`}>
            <div className='hud__player_box_info'>
              <div className='hud__player_box_voice'>
                <div className={`hud__player_box_voice_img ${this.state.voice ? 'voice_on' : 'voice_off'}`}></div>
                <div className={`hud__player_box_voice_block ${this.state.voice ? 'voice_block_on' : 'voice_block_off'}`}></div>
              </div>
              <div className='hud__player_box_info_box'>
                <div className='hud__player_box_info_box_title'>Наличные</div>
                <div className='hud__player_box_info_box_numbers'>{this.state.money} $</div>
                <div className='hud__player_box_info_box_title'>Время</div>
                <div className='hud__player_box_info_box_numbers'>
                  {this.state.time} {this.state.date}
                </div>
              </div>
            </div>
            <CSSTransition
              in={this.state.speedometer_visible}
              nodeRef={this.nodeRef2}
              timeout={{
                appear: 100,
                enter: 3900,
                exit: 500,
              }}
              classNames='fade'
              unmountOnExit
            >
              <div className='hud__speed_main' ref={this.nodeRef2}>
                <div className={`hud__speed_key hud__color_img_${this.state.key ? 'on' : 'off'}`}></div>
                <div className='hud__speed_img'>
                  <div className='hud__speed_blur'></div>
                </div>
                <div className='hud__speed_info'>
                  <div className='hud__speed_title'>Скорость</div>
                  <div className='hud__speed_text'>
                    <span className='hud__speed_text_big'>{this.state.speed}</span>
                    <span className='hud__speed_text_small'>km/h</span>
                  </div>
                </div>
              </div>
            </CSSTransition>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default PlayerHud;
