import React from 'react';

class Main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <React.Fragment>
        <div className='menup__main_grid_box'>
          {Object.values(this.props.data).map((e, i) => {
            let index = `main_box${i}`;
            return (
              <div className='menup__main_page_box' key={index}>
                <div className='menup__main_page_box_title'>{e.title}</div>
                <div className='menup__main_page_box_text'>{e.value}</div>
                {e.note !== '' && e.note !== undefined ? <div className='menup__main_page_box_text_bottom'>{e.note}</div> : null}
              </div>
            );
          })}
        </div>
      </React.Fragment>
    );
  }
}

export default Main;
