import React from 'react';
import EventManager from '../../../EventManager';

class HelpMenu extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,
      help_menu: [
        { button: 'N', text: 'Голосовой чат' },
        { button: 'F5', text: 'Скрыть интерфейс' },
        { button: 'F6', text: 'Скрыть подсказки' },
      ],
    };
  }

  componentDidMount() {
    EventManager.addHandler('HelpMenu', (value) => {
      if (value.type === 'show') {
        this.setState({ show: true });
      }
      if (value.type === 'hide') {
        this.setState({ show: false });
      } else return;
    });
  }

  componentWillUnmount() {
    EventManager.removeHandler('HelpMenu');
  }

  render() {
    if (!this.state.show) {
      return null;
    }
    return (
      <React.Fragment>
        <div className='hud__helpmenu'>
          {this.state.help_menu.map((e, i) => {
            let index = `helpmenu${i}`;
            return (
              <div className='hud__helpmenu__box_ul' key={index}>
                <div className='hud__helpmenu__box_btn'>{e.button}</div>
                <span>{e.text}</span>
              </div>
            );
          })}
        </div>
      </React.Fragment>
    );
  }
}

export default HelpMenu;
