import React from 'react';

class Progress extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <React.Fragment>
        <div className='menup__progress_main'>
          {Object.values(this.props.data.achievements).map((e, i) => {
            let index = `progress_box${i}`;
            return (
              <div className='menup__progress_main_box' key={index}>
                <div className='menup__progress_main_box_left'>
                  <div className='menup__progress_main_box_left_head'>
                    <div className='menup__progress_main_title'>{e.title}</div>
                    <div
                      className='menup__progress_main_bar'
                      data-label={
                        parseInt(e.current) >= parseInt(e.total) || e.unlocked ? `Выполнено` : parseInt(e.current) === 0 && parseInt(e.total) === 1 ? `Не выполнено` : `${e.current}/${e.total}`
                      }
                    >
                      <div className='menup__progress_main_bar_line' style={{ width: `${(parseInt(e.current) / parseInt(e.total)) * 100}%` }}></div>
                    </div>
                  </div>
                  <div className='menup__progress_main_text'>{e.note}</div>
                </div>
                <input
                  type='button'
                  value='Забрать приз'
                  onClick={() => this.props.unlockAchievement(e.id)}
                  disabled={parseInt(e.current) < parseInt(e.total) || e.unlocked}
                  className='menup__progress_main_box_button'
                />
              </div>
            );
          })}
        </div>
      </React.Fragment>
    );
  }
}

export default Progress;
