import React from 'react';
import { CSSTransition } from 'react-transition-group';

class Quest extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      quest: {
        name: 'Квест',
        title: 'Убей придателя',
        text: 'Отыщи и прикончи предателя',
        task: 'Убийство',
        task_text: ` 1 <span style="color: #00A3B5">/1</span>`,
      },
    };
    this.nodeRef = React.createRef();
  }

  render() {
    return (
      <React.Fragment>
        <CSSTransition in={this.state.show} nodeRef={this.nodeRef} timeout={900} classNames='fade_quest' unmountOnExit>
          <div className='hud__quest_main' ref={this.nodeRef}>
            <div className='hud__quest_header'>
              <div className='hud__quest_header_name'>{this.state.quest.name}</div>
              <div className='hud__quest_header_title'>{this.state.quest.title}</div>
              <div className='hud__quest_header_text'>{this.state.quest.text}</div>
            </div>
            <div className='hud__quest_bottom'>
              <div className='hud__quest_bottom_title'>{this.state.quest.task}</div>
              <div className='hud__quest_bottom_text' dangerouslySetInnerHTML={{ __html: `${this.state.quest.task_text}` }}></div>
            </div>
          </div>
        </CSSTransition>
      </React.Fragment>
    );
  }
}

export default Quest;
