import React from 'react';

import Collapsible from 'react-collapsible';

class Rules extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <React.Fragment>
        <div className='menup__rules_box'>
          {Object.values(this.props.data).map((e, i) => {
            let index = `rules${i}`;
            return (
              <Collapsible
                key={index}
                trigger={e.title}
                classParentString='menup__rules_main'
                className='menup__rules_main'
                openedClassName='menup__rules_main_text'
                contentInnerClassName='menup__rules_main_text_inner'
                triggerClassName='menup__rules_main_title'
                triggerOpenedClassName='menup__rules_main_title'
                contentOuterClassName='menup__rules_main_text'
              >
                {e.text}
              </Collapsible>
            );
          })}
        </div>
      </React.Fragment>
    );
  }
}

export default Rules;
