import React from 'react';

import EventManager from '../../EventManager';
import './css/carpark.css';

class CarPark extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      rank_player: 0,
      select_car: 0, // всегда выбрана 1 машина
      car: {
        0: { id: 0, name: 'BMX', rank: 1 },
        1: { id: 1, name: 'Peyote', rank: 1 },
        2: { id: 2, name: 'Tornado', rank: 1 },

        3: { id: 3, name: 'Manana', rank: 2 },
        4: { id: 4, name: 'Emperor', rank: 2 },

        5: { id: 5, name: 'Voodoo', rank: 3 },

        6: { id: 6, name: 'Picador', rank: 4 },
        7: { id: 7, name: 'Moonbeam', rank: 4 },

        8: { id: 8, name: 'Hexer', rank: 5 },
        9: { id: 9, name: 'Sovereign', rank: 5 },

        10: { id: 10, name: 'Yosemite', rank: 6 },
        11: { id: 11, name: 'Faction', rank: 6 },
        12: { id: 12, name: 'Warrener', rank: 6 },

        13: { id: 13, name: 'Buccaneer', rank: 7 },
        14: { id: 14, name: 'Riata', rank: 7 },

        15: { id: 15, name: 'Riata', rank: 8 },
        16: { id: 16, name: 'Cognoscenti', rank: 8 },

        17: { id: 17, name: 'Gauntlet', rank: 9 },

        18: { id: 18, name: 'Superd', rank: 10 },
        19: { id: 19, name: 'Dubsta', rank: 10 },
        20: { id: 20, name: 'Dominator', rank: 10 },
      },
    };
  }

  componentDidMount() {
    EventManager.addHandler('ShowCarPark', (value) => {
      this.setState((prev) => ({
        show: true,
        rank_player: value.playerRank,
        select_car: value.currentVehicleId,
      }));
    });

    EventManager.addHandler('CloseCarPark', (value) => {
      this.setState((prev) => ({
        show: false,
      }));
    });

    EventManager.addHandler('UpdateSelectedCar', (value) => {
      this.setState((prev) => ({
        select_car: value,
      }));
    });
  }

  componentWillUnmount() {
    EventManager.removeHandler('ShowCarPark');
    EventManager.removeHandler('CloseCarPark');
    EventManager.removeHandler('UpdateSelectedCar');
  }

  selectCar(id) {
    this.setState({ select_car: id });
    mp.trigger('CarSelected', id); // eslint-disable-line
  }

  takeCar() {
    mp.trigger('TakeCar'); // eslint-disable-line
  }

  closeMenu() {
    mp.trigger('CloseCarPark'); // eslint-disable-line
  }

  render() {
    if (!this.state.show) {
      return null;
    }
    return (
      <React.Fragment>
        <div className='car__background'>
          <div className='car__menu_box'>
            <div className='car__menu_title'>Автопарк</div>
            <div className='car__menu_close' onClick={() => this.closeMenu()}></div>
            <div className='car__menu_list'>
              {Object.values(this.state.car)
                .sort((a, b) => a.rank - b.rank)
                .map((e, i) => {
                  let index = `car${i}`;
                  return (
                    <div className='car__menu_ul_box' key={index}>
                      <input
                        type='radio'
                        id={index}
                        name='carpark'
                        disabled={this.state.rank_player < e.rank ? true : false}
                        checked={this.state.select_car === e.id}
                        onChange={() => this.selectCar(e.id)}
                      />
                      <label htmlFor={index} className='car__menu_ul'>
                        <div className='car__menu_ul_blur'></div>
                        {e.name}
                        {this.state.rank_player < e.rank ? <span className='car__menu_ul_text'>(с {e.rank} ранга)</span> : null}
                      </label>
                    </div>
                  );
                })}
            </div>
          </div>
          <div className='car__button' onClick={() => this.takeCar()}>
            <div className='car__button_blur'></div>
            Выбрать
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default CarPark;
