import React from 'react';

class Box extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    let image;
    try {
      image = require(`../img/items/${this.props.item.id}.png`).default;
    } catch (e) {
      image = require(`../img/items/noImage.png`).default;
    }

    return (
      <React.Fragment>
        <div className='wshop_grid_box'>
          <div className='wshop_grid_box_blur'></div>
          <div className='wshop_grid_box_title'>
            <div className='wshop_grid_box_title_left'>
              <div className='wshop_grid_box_title_left_text_grey'>Название</div>
              <div className='wshop_grid_box_title_left_text_white'>{this.props.items[this.props.category][this.props.item.id].name}</div>
            </div>
            <div className='wshop_grid_box_title_right'>
              <div className='wshop_grid_box_title_right_top'>
                <div className='wshop_grid_box_title_right_text_grey'>Урон</div>
                <div className='wshop_grid_box_title_right_text_white'>{this.props.items[this.props.category][this.props.item.id].damage}</div>
              </div>
              <div className='wshop_grid_box_title_right_bar'>
                <div className='wshop_grid_box_title_right_line' style={{ width: `${this.props.items[this.props.category][this.props.item.id].damage}%` }}></div>
              </div>
            </div>
          </div>
          <div className='wshop__grid_img' style={{ backgroundImage: `url(${image})` }}></div>
          <div className='wshop_grid_box_title_left_text_grey'>Цена</div>
          <div className='wshop__grid_row'>
            <div className='wshop__grid_row_box_price'>{this.props.item.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}$</div>
            <div className='wshop__grid_row_box_buy' onClick={() => this.props.buyItem(this.props.item)}>
              Купить
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Box;
