import React from 'react';
import EventManager from '../../../EventManager';

class Register extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      input_email: '',
      input_username: '',
      input_password: '',
      input_confirm_password: '',

      password1_visibility: false,
      password2_visibility: false,

      errors: {},
    };

    this.errors_mapping = new Map();
    this.errors_mapping.set('email', ['email', 'Введи существующий email']);
    this.errors_mapping.set('username', ['username', 'Никнейм должен содержать минимум 1, максимум 32 символов']);
    this.errors_mapping.set('password', ['password', 'Пароль должен содержать минимум 6, максимум 50 символов']);
    this.errors_mapping.set('confirm_password', ['confirm_password', 'Пароли должны совпадать']);
    this.errors_mapping.set('email_already_exists', ['email', 'Email уже занят']);
    this.errors_mapping.set('username_already_exists', ['username', 'Никнейм уже занят']);
    this.errors_mapping.set('internal_server_error', ['email', 'Что-то пошло не так, попробуй еще раз']);
    this.errors_mapping.set('wait', ['email', 'Подожди перед повторной попыткой']);
  }

  deleteError(field) {
    let errs = this.state.errors;
    delete errs[field];
    this.setState({ errors: errs });
  }

  componentDidUpdate(prevProp, prevState) {
    if (this.state.input_email !== prevState.input_email) {
      this.deleteError('email');
    }

    if (this.state.input_username !== prevState.input_username) {
      this.deleteError('username');
    }

    if (this.state.input_password !== prevState.input_password) {
      this.deleteError('password');
    }

    if (this.state.input_confirm_password !== prevState.input_confirm_password) {
      this.deleteError('confirm_password');
    }
  }

  componentDidMount() {
    EventManager.addHandler('RegisterFailed', (value) => {
      let errs = {};

      value.forEach((item, _) => {
        let err = this.errors_mapping.get(item);
        errs[err[0]] = err[1];

        this.setState({ errors: errs });
      });
    });

    document.addEventListener('keydown', this.handleKeyPress);
  }

  componentWillUnmount() {
    EventManager.removeHandler('RegisterFailed');

    document.removeEventListener('keydown', this.handleKeyPress);
  }

  handleKeyPress = (event) => {
    if (event.key === 'Enter') {
      this.register();
    }
  };

  register() {
    let request = {
      email: this.state.input_email,
      username: this.state.input_username,
      password: this.state.input_password,
      confirmPassword: this.state.input_confirm_password,
    };

    mp.trigger('RegisterSubmitted', JSON.stringify(request)); // eslint-disable-line
  }

  render() {
    return (
      <React.Fragment>
        <div className='signup__main'>
          <div className='signup__title'>Регистрация</div>
          <div className='signup__title_text'>Для игры на сервере необходимо зарегистрироваться</div>
          <div className='signup__grid_box'>
            <div className={`signup__input_box ${this.state.errors.email ? 'signup__input_failed' : null}`}>
              <input type='text' className='signup__input' placeholder='Эл.почта' spellCheck='false' value={this.state.input_email} onChange={(e) => this.setState({ input_email: e.target.value })} />
              {this.state.errors.email ? (
                <div className='sigin__main_warning_box'>
                  <div className='sigin__warning_box'>
                    <div className='signin__img_failed'></div>
                  </div>
                  <div className='sigin_box_popup_text'>
                    <div className='sigin_popup_text'>{this.state.errors.email}</div>
                  </div>
                </div>
              ) : null}
            </div>
            <div className={`signup__input_box ${this.state.errors.username ? 'signup__input_failed' : null}`}>
              <input
                type='text'
                className='signup__input'
                placeholder='Никнейм'
                spellCheck='false'
                value={this.state.input_username}
                onChange={(e) => this.setState({ input_username: e.target.value.replace(/[^a-zA-Z0-9]+/g, '') })}
              />
              {this.state.errors.username ? (
                <div className='sigin__main_warning_box'>
                  <div className='sigin__warning_box'>
                    <div className='signin__img_failed'></div>
                  </div>
                  <div className='sigin_box_popup_text'>
                    <div className='sigin_popup_text'>{this.state.errors.username}</div>
                  </div>
                </div>
              ) : null}
            </div>
            <div className={`signup__input_box  ${this.state.errors.password ? 'signup__input_failed' : null}`}>
              <input
                type={this.state.password1_visibility ? 'text' : 'password'}
                className='signup__input'
                placeholder='Пароль'
                spellCheck='false'
                value={this.state.input_password}
                onChange={(e) => this.setState({ input_password: e.target.value })}
              />
              {this.state.errors.password ? (
                <div className='sigin__main_warning_box'>
                  <div className='sigin__warning_box'>
                    <div className='signin__img_failed'></div>
                  </div>
                  <div className='sigin_box_popup_text'>
                    <div className='sigin_popup_text'>{this.state.errors.password}</div>
                  </div>
                </div>
              ) : (
                <div
                  className='signup__input_visibility'
                  style={{ backgroundImage: `url(${require(`../img/icons/${this.state.password1_visibility ? 'visibility_on' : 'visibility_off'}.svg`).default})` }}
                  onClick={() => this.setState({ password1_visibility: !this.state.password1_visibility })}
                ></div>
              )}
            </div>
            <div className={`signup__input_box ${this.state.errors.confirm_password ? 'signup__input_failed' : null}`}>
              <input
                type={this.state.password2_visibility ? 'text' : 'password'}
                className='signup__input'
                placeholder='Повтор пароля'
                value={this.state.input_confirm_password}
                spellCheck='false'
                onChange={(e) => this.setState({ input_confirm_password: e.target.value })}
              />
              {this.state.errors.confirm_password ? (
                <div className='sigin__main_warning_box'>
                  <div className='sigin__warning_box'>
                    <div className='signin__img_failed'></div>
                  </div>
                  <div className='sigin_box_popup_text'>
                    <div className='sigin_popup_text'>{this.state.errors.confirm_password}</div>
                  </div>
                </div>
              ) : (
                <div
                  className='signup__input_visibility'
                  style={{ backgroundImage: `url(${require(`../img/icons/${this.state.password2_visibility ? 'visibility_on' : 'visibility_off'}.svg`).default})` }}
                  onClick={() => this.setState({ password2_visibility: !this.state.password2_visibility })}
                ></div>
              )}
            </div>
          </div>
          <div className='signup__button' onClick={() => this.register()}>
            <div className='signup__button_blur'></div>
            <span className='signup__button_text'>Зарегистрироваться</span>
          </div>
          <div className='signup__text_signin' onClick={() => this.props.historyPush('/auth/login')}>
            Есть аккаунт? Авторизуйся!
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Register;
